*{
  margin: 0 auto;
  text-decoration-line: none;
  background-color: white;
}
.Header{
  width: 400px;
  text-align: center;
  border: 2px solid grey;
  border-radius: 10px;
  padding: 30px;
  margin-top: 30px;
}

.Header img{
  width: 100%;
  border-radius: 10px;
}

nav{
  font-size: 30px;
  font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

nav ul{
  padding: 0;
  margin: 0 auto;
  display: flex;
  list-style: none;
}

.Text{
  width: 100%;
  border: 2px solid black;
  height: 300px;
  padding: 40px 0;
}

.mainName{
  text-align: center;
  margin: 30px;
}

.body{
border: 1px solid black;
margin : 30px;
}

.Head{
  text-align: center;
  font-size: 20px;
}

li{
  border: 3px solid #eee;
  border-radius: 10px;
  padding: 5px;
 
}

.Topbar, .footer {
  display: flex;
  justify-content: space-around; 
  align-items: center; 
  padding: 10px; 
}

.footer {
  flex-direction: column;
}

.footer_div1 {
  margin-bottom: 10px;
}

.Toptop li{
  margin: 20px;
  transition: transform 0.2s ease-in-out;
}
.Toptop li:hover {
  transform: scale(1.05); /* 호버 시 크기를 1.05배로 확대 */
  background-color: #eee;
  
}

.CommunityMain{
  text-align:center;
  padding: 30px;
  border: 3px solid darkgrey;
  margin: 30px;
  font-size: 30px;
  border-radius: 30px;
  background-color: #eee;
}

html, body {
	margin: 0 auto;
	box-sizing: border-box;
	height: 100%;
}

.footer {
    color: black;
    background-color: #b6d7e5;
    margin: 140px auto 0 auto;
    border-top: 2px solid #eee;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 35px;
}

.footer_div1 {
	padding: 20px;
	display: flex;
	justify-content: space-evenly;
}

.footer a {
    text-decoration-line: none;
    color: black;
    font-size: smaller;
}

.footer p {
	font-size: xx-small;
	text-align: center;
}

.footer p:nth-child(2) {
	padding-bottom: 10px;
}

.footer a:hover {
	color: blue;
}

#youtube_logo {
	width: 20px;
}

table {
  border-spacing: 10px; 
  width: 100%;
}

th, td {
  padding: 10px; 
  text-align: center;
}

th{
 text-decoration: 4px underline #eee; 
}

.communityTbody{
  cursor: pointer;
}

button:hover{
  background-color: grey;
}


.mainbody{
  text-align: center;
  font-size: 30px;
}
.community-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

button{
  border-radius: 10px;
  background-color: #eee;
  color: black;
  border: none;
  cursor: pointer;
  width: 200px;
  height: 30px;
  border: 1px solid black;
}

letsgobabies{
  display: flex;
  text-align: center;
}

.ClassInfo{
  border: 1px solid black;
  border-radius: 10px; 
  margin: 10px 80px; 
  padding: 5px;
  display: flex;
  flex-direction: column;
  
}